<template>
  <div>
    <div v-if="isMedia" class="media__nav d-flex flex-column align-end">
      <v-icon 
        size="48" 
        class="media__nav__icon" 
        @click.stop="isOpen = !isOpen"
      >menu</v-icon>
      <v-navigation-drawer v-if="isOpen && isMedia" class="navigation__menu" dark permanent>
        <v-list>
          <v-list-item class="navigation__menu__media" @click="$router.push('/')" link>
            <v-list-item-icon>
              <v-icon>home</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title class="navigation__text">Моя страница</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <div 
            class="navigation__menu__media"
            v-for="(item, index) in items" 
            :key="index" link>
            <v-list-item @click="linkRel(item)">
              <v-list-item-icon>
                <v-badge bordered color="error" icon="mdi-lock" overlap v-if="item.isBadges">
                <v-icon>{{ item.icon }}</v-icon>
                </v-badge>
                <v-icon v-else>{{ item.icon }}</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title class="navigation__text">{{ item.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </div>
        </v-list>
      </v-navigation-drawer>
    </div>
    <v-card v-else class="navigation">
      <v-card-title class="navigation__title">Меню</v-card-title>
      <v-navigation-drawer class="navigation__menu" dark permanent>
        <v-list>
          <v-list-item class="navigation__menu__media" @click="$router.push('/')" link>
            <v-list-item-icon>
              <v-icon>home</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title class="navigation__text">Моя страница</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <div
            class="navigation__menu__media"
            v-for="(item, index) in items" 
            :key="index" link>
            <v-list-item @click="linkRel(item)">
              <v-list-item-icon>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title class="navigation__text">{{ item.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </div>
        </v-list>
      </v-navigation-drawer>
    </v-card>
    <v-dialog v-model="isLeave" persistent max-width="900px">
      <v-card style="padding: 50px;" class="navigation__leave d-flex flex-column align-center">
        <v-img src="../assets/nopost.png" width="300" height="200"></v-img>
        <v-card-title class="navigation__leave__title">
          Вы уверены, что хотите выйти?
        </v-card-title>
        <v-card-actions>
          <v-btn color="error" @click="userLeave">
            Выйти
          </v-btn>
          <v-btn style="color: #fff;" color="myPurple" @click="isLeave = false">
            Вернуться
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import "../scss/mediaAccount.css";

export default {
  name: "Panel",
  data() {
    return {
      user: "",
      isLeave: false,
      isMedia: false,
      isOpen: false,
      items: [
        { text: "Моя лента", icon: "library_books", link: "News" },
        { text: "Мои чаты", icon: "question_answer", link: "Chats" },
        { text: "Мои друзья", icon: "person", link: "FriendsList" },
        // { text: "Мои паблики", icon: "supervisor_account", link: "GroupList" },
        { text: "Моя галерея", icon: "collections", link: "Gallery" },
        { text: "Мои Аудиозаписи", icon: "volume_up", link: "Audio" },
        { text: "Мои сохраненки", icon: "bookmarks", link: "Bookmarks" },
        { text: "Мои настройки", icon: "mdi-wrench", link: "Edit" },
        { text: "Выйти", icon: "exit_to_app", link: "Home" }
      ]
    }
  },
  created() {
    this.user = JSON.parse(localStorage.getItem("user"))
    window.addEventListener("resize", this.updateWidth)
    let userWidth = document.documentElement.clientWidth
    if (userWidth <= 1400) this.isMedia = true
    else this.isMedia = false
  },
  methods: {
    linkRel(item) {
      if (item.text == "Выйти") this.isLeave = true
      else this.$router.push({ name: item.link });
      
      if (item.text == "Моя галерея") 
        this.$router.push({ name: "Gallery", params: { id: this.user.id }})
      if (item.text == "Мои Аудиозаписи") 
        this.$router.push({ name: "Audio", params: { id: this.user.id }})
    },
    userLeave() {
      localStorage.clear()
      this.$router.push({ name: 'Home' });
    },
    updateWidth() {
      let userWidth = document.documentElement.clientWidth
      if (userWidth <= 1400) this.isMedia = true
      else this.isMedia = false
    }
  }
};
</script>

<style lang="scss" scoped>
.navigation {
  background: #fff;
  max-height: 700px;
  margin-right: 30px;
  min-width: 100px;
  max-width: 220px;
  &__title {
    font-size: 26px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
  &__text {
    font-size: 12px;
  }
  &__menu {
    background: #7f5af0 !important;
  }
}
.media {
  &__nav {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 9999;
    max-width: 200px;
    min-height: 300px;
    &__icon {
      background-color: #2cb67d;
      padding: 11px;
    }
  }
}
</style>